










































export default {
	data() {
		return {
			background: ['color1', 'color2', 'color3'],
			indicatorDots: true,
			autoplay: false,
			duration: 500,
			baise:'rgba(255, 255, 255, .3)',
			lanse: '#918984'
		}
	},
	methods: {
		launchFlag: function(){
			/**
			 * 向本地存储中设置launchFlag的值，即启动标识；
			 */
			uni.setStorage({
					key: 'launchFlag',
					data: true,
			});
			uni.switchTab({
				url: '../tabbar/tabbar-1/tabbar-1'
			});
			
		}
	}
}
